@mixin flex-row {
  display: flex;
  flex-direction: row;
  align-items: center;
}
@mixin flex-col {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@mixin index-css {
  @include flex-col;
  height: 100%;
  padding: 10px 20px;
  box-sizing: border-box;
}

$theme: #ee7979;
$grey: #8a8a8a;
$line-grey: #f5f5f5;

$big-font: 220%;
$medium-font: 135%;
$small-font: 105%;
$xs-font: 80%;

.index {
  @include index-css;
}
.calendar {
  @include flex-col;
  width: 85%;
  height: 45%;
  color: $grey;
  box-shadow: 2px 2px 12px $grey;
}
.calendar-header {
  @include flex-row;
  justify-content: space-around;
  color: white;
  font-size: 110%;
  height: 80px;
  width: 100%;
  background-color: $theme;
  img {
    width: 30px;
    height: 30px;
  }
}
.calendar-body {
  @include flex-col;
  justify-content: space-evenly;
  width: 100%;
  height: 100%;
}
.calendar-week {
  @include flex-row;
  width: 100%;
}
.calendar-week-day {
  width: calc(100% / 7);
  text-align: center;
}
.curMonth {
  color: black;
  text-decoration: none;
}
.curDate {
  color: $theme;
  text-decoration: none;
}

.next {
  @include flex-col;
  width: 100%;
}
.next-title {
  width: 100%;
  font-size: $big-font;
  font-weight: 100;
  margin: 30px 0;
}
.next-content {
  @include flex-row;
  width: 90%;
  img {
    width: 50px;
    height: 50px;
    margin-right: 20px;
  }
}
.next-content-text {
  @include flex-col;
  align-items: flex-start;
}
.next-content-text-title {
  font-size: $medium-font;
  font-weight: 500;
}
.next-content-text-note {
  color: $grey;
}
