@mixin flex-row {
  display: flex;
  flex-direction: row;
  align-items: center;
}
@mixin flex-col {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@mixin index-css {
  @include flex-col;
  height: 100%;
  padding: 10px 20px;
  box-sizing: border-box;
}

$theme: #ee7979;
$grey: #8a8a8a;
$line-grey: #f5f5f5;

$big-font: 220%;
$medium-font: 135%;
$small-font: 105%;
$xs-font: 80%;

.index {
  @include flex-row;
  position: absolute;
  bottom: 20px;
  right: 20px;
  font-weight: bold;
  img {
    width: 30px;
    height: 30px;
  }
}
