@mixin flex-row {
  display: flex;
  flex-direction: row;
  align-items: center;
}
@mixin flex-col {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@mixin index-css {
  @include flex-col;
  height: 100%;
  padding: 10px 20px;
  box-sizing: border-box;
}

$theme: #ee7979;
$grey: #8a8a8a;
$line-grey: #f5f5f5;

$big-font: 220%;
$medium-font: 135%;
$small-font: 105%;
$xs-font: 80%;

.index {
  height: 100%;
}
.background {
  @include flex-col;
  justify-content: space-between;
  background-size: cover;
  background-position: center center;
  color: white;
  height: 25%;
  padding: 10px;
  box-sizing: border-box;
}
.background-title {
  width: 100%;
  font-size: $big-font;
}
.background-content {
  @include flex-col;
  align-items: flex-end;
  width: 100%;
  font-size: $medium-font;
  font-family: consolas;
  text-shadow: 1px 1px 1px grey;
}

.note {
  padding: 20px;
  font-weight: 100;
}
.note-title {
  font-size: $medium-font;
  margin-bottom: 10px;
}

.buttons {
  @include flex-col;
  justify-content: space-evenly;
  width: 100%;
  height: 30%;
  div {
    @include flex-col;
    justify-content: center;
    width: 80%;
    height: 20%;
    box-sizing: border-box;
  }
}
.buttons-done {
  background-color: $theme;
  color: white;
}
.buttons-del {
  background-color: $grey;
  color: white;
}
.buttons-edit {
  background-color: white;
  border: 1px solid $grey;
  color: $grey;
}
