@mixin flex-row {
  display: flex;
  flex-direction: row;
  align-items: center;
}
@mixin flex-col {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@mixin index-css {
  @include flex-col;
  height: 100%;
  padding: 10px 20px;
  box-sizing: border-box;
}

$theme: #ee7979;
$grey: #8a8a8a;
$line-grey: #f5f5f5;

$big-font: 220%;
$medium-font: 135%;
$small-font: 105%;
$xs-font: 80%;

.index {
  @include flex-col;
  width: 100%;
  position: absolute;
  top: 45%;
  display: none;
  transition: background-color 2s;
  -moz-transition: background-color 2s; /* Firefox 4 */
  -webkit-transition: background-color 2s; /* Safari 和 Chrome */
  -o-transition: background-color 2s; /* Opera */
}
.index-text {
  @include flex-row;
  justify-content: center;
  background-color: #5e5e5ee3;
  color: white;
  border-radius: 5px;
  padding: 0 10px;
  height: 30px;
}
