@mixin flex-row {
  display: flex;
  flex-direction: row;
  align-items: center;
}
@mixin flex-col {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@mixin index-css {
  @include flex-col;
  height: 100%;
  padding: 10px 20px;
  box-sizing: border-box;
}

$theme: #ee7979;
$grey: #8a8a8a;
$line-grey: #f5f5f5;

$big-font: 220%;
$medium-font: 135%;
$small-font: 105%;
$xs-font: 80%;

.index {
  @include flex-col;
  width: 100%;
}
.mask {
  background-color: rgba($color: #000000, $alpha: 0.4);
  position: absolute;
  height: 100%;
  width: 100%;
}
.wrapper {
  @include flex-col;
  justify-content: space-between;
  position: absolute;
  top: 33%;
  background-color: white;
  width: 70%;
  border-radius: 10px;
  height: 150px;
}
.title {
  @include flex-row;
  align-items: flex-end;
  height: 30%;
  font-weight: bold;
  font-size: $small-font;
}
.content {
  color: $grey;
}
.buttons {
  @include flex-row;
  justify-content: space-between;
  width: 100%;
  height: 35%;
  border-top: 1px solid $line-grey;
  div {
    @include flex-col;
    justify-content: center;
    width: 50%;
    height: 100%;
  }
}
.buttons-cancel {
  border-right: 1px solid $line-grey;
}
.buttons-confirm {
  color: $theme;
}
